import React, {Component} from 'react';
import './App.css';
import currService from './services/currservice'
import Tabs from './components/Tabs'
import 'simplebar/dist/simplebar.css';
import day from './images/outline-brightness_day.svg'
import night from './images/outline-brightness_night.svg'
import Loading from './components/Loading'

 

export default class App extends Component {

   currService = new currService()


   state = {
      items: [],
      golds: [],
      error: false,
      loading: true,
      theme: ''
   }
   

   componentDidMount() {
      this.setLightTheme()
      this.updateCurrs()
      this.updateGolds()
   }

   updateCurrs = () => {
		
      const type = 0;
      this.currService.getCurrencies(type)
      .then(this.onItemsLoaded)
      .catch(this.onError)
      
   }
   updateGolds = () => {
		
      const type = 1;
      this.currService.getCurrencies(type)
      .then(this.onGoldsLoaded)
      .catch(this.onError)
      
   }

   onItemsLoaded = items => {
      this.setState({
         items,
         error: false,
         loading: false
      })
   }

   onGoldsLoaded = items => {
      this.setState({
         golds: items,
         error: false,
         loading: false
      })
   }

   onError = (err) => {
		console.log(err)
		
		this.setState({
			error: true
		})
   }
   setDarkTheme = async () => {
      await this.setState({theme: 'night'})
      this.reloadTheme(this.state.theme);

   }
   setLightTheme = async () => {
      await this.setState({theme: 'day'})
      this.reloadTheme(this.state.theme);

   }
   reloadTheme = (whichTheme) => {
      if  (whichTheme === 'night') {
          document.querySelector('body').style.setProperty('--background', '#202124');
          document.querySelector('body').style.setProperty('--text-color', 'white');
          document.querySelector('body').style.setProperty('--black', 'white');
          document.querySelector('body').style.setProperty('--border-color', 'rgb(95, 99, 104)');
          document.querySelector('body').style.setProperty('--z-depth-1', '0 1px 2px 0 rgba(0, 0, 0, .60), 0 1px 3px 1px rgba(0, 0, 0, .30)');
          document.querySelector('body').style.setProperty('--grey-color', '#ADACB1');
          document.querySelector('body').style.setProperty('--second-bg', 'rgb(255,255,255,.04)');
          document.querySelector('body').style.setProperty('--tab-text-color', '#ADACB1');
          document.querySelector('body').style.setProperty('--tab-active-color', '#8ab4f8');
          document.querySelector('.switchTheme__img--night').style.setProperty('display', 'block')
          document.querySelector('.switchTheme__img--day').style.setProperty('display', 'none')

      }
      else if (whichTheme === 'day') {
          document.querySelector('body').style.removeProperty('--background')
          document.querySelector('body').style.removeProperty('--text-color')
          document.querySelector('body').style.removeProperty('--black')
          document.querySelector('body').style.removeProperty('--border-color')
          document.querySelector('body').style.removeProperty('--z-depth-1')
          document.querySelector('body').style.removeProperty('--grey-color')
          document.querySelector('body').style.removeProperty('--second-bg')
          document.querySelector('body').style.removeProperty('--tab-text-color')
          document.querySelector('body').style.removeProperty('--tab-active-color')
          document.querySelector('.switchTheme__img--night').style.setProperty('display', 'none')
          document.querySelector('.switchTheme__img--day').style.setProperty('display', 'block')
      }

   }

   renderTable(headTitle, fun){
      return(
         <table className="mdTable">
            <thead className="mdTable__header">
               <tr className="mdTable__header--row">
                  <th className="mdTable__header--th">{headTitle}</th>
                  <th className="mdTable__header--th">Alış</th>
                  <th className="mdTable__header--th">Satış</th>
               </tr>
            </thead>
            <tbody className="mdTable__body">
               {fun}
            </tbody>
         </table>
      )
   }
   
   renderItems(arr) {
		
		
		return arr.map((items, index) => {
			
			const {title, alis, satis} = items;
			return(
            <tr key={index} className="mdTable__header--row mdTable__inner--row">
               <td className="mdTable__header--td">{title}</td>
               <td className="mdTable__header--td number">{alis.toFixed(3)} ₺</td>
               <td className="mdTable__header--td number">{satis.toFixed(3)} ₺</td>
            </tr>
			)
				
		});
   }
   
   loading() {
      let isDay = this.state.theme;
      if (isDay === 'day') {
         isDay = true
      }
      else if(isDay === 'night') {
         isDay = false
      }
      return(
         <Loading theme={isDay}/>
      )
   }

   tabRender = () => {
      const {items, golds, loading} = this.state

      let currs = this.renderTable('Döviz İsmi', this.renderItems(items));
      let gold = this.renderTable('Altın birimi', this.renderItems(golds));

      if (!loading) {
         return(
            <Tabs datas={[
               {
                  title: 'Dövizler',
                  content: currs,
                  active: true,
               },
               {
                  title: 'Altın', 
                  content: gold,
               },
            ]}/>
         )
      
      }
      else {
         return (this.loading())
      }

      
   }
   render() {
      const {error} = this.state

      if (error) {
         console.log('Renderdaki Hata')
      }

      return(
         <div className="currency">
            <span className="switchTheme">
               <img src={day} onClick={this.setDarkTheme} className="switchTheme__img switchTheme__img--day" alt="theme switcher"/>
               <img src={night} onClick={this.setLightTheme} className="switchTheme__img switchTheme__img--night" alt="theme switcher" style={{display: 'none'}}/>
            </span>

            {this.tabRender()}


         </div>
      )
      
      
      
   }
}
