import React from 'react';
import './Tabs.css'



export default function Tabs(props) {

    function initTab(event) {
        event.preventDefault();

        document.querySelectorAll('.tabs__inner--item').forEach((tab) => {
            tab.classList.remove('tabs__inner--active')
        });
        document.querySelectorAll('.tabs__header--link').forEach((link) => {
            link.classList.remove('tabs__header--active')
        });
        
        var tabID = new URL(event.target.href);

        document.querySelector('a[href="' + tabID.hash + '"]').classList.add('tabs__header--active')
        document.querySelector(tabID.hash).classList.add('tabs__inner--active')

    }
    function renderHeaders(arr) {
        return arr.map((items, index) => {
            const {title, active} = items;

            return(
                <li key={index} className="tabs__header--item">
                    <a role="button" className={"tabs__header--link" + (active ? ' tabs__header--active' : '')} href={"#tab" + index} onClick={initTab}>{title}</a>
                </li>
            )
        });
    }
    function renderContents(arr) {
        return arr.map((items, index) => {
            const {content, active} = items;
            return(
                <div key={index} id={"tab" + index} className={"tabs__inner--item" + (active ? ' tabs__inner--active' : '')}>{content}</div>
            )
        });
    }
    

    return(
        <div className="tabs">
            <ul className="tabs__header">
                {renderHeaders(props.datas)}
            </ul>
            <div className="tabs__inner">
                {renderContents(props.datas)}
            </div>
                
        </div>
        
    )
}