export default class currService {

    constructor() {

        this.currencies = `https://api.canlidoviz.com/web/items?marketId=1&type=0`
        this.gold = `https://api.canlidoviz.com/web/items?marketId=1&type=1`

    }


    fetchFun = async (url) => {

        const res = await fetch(`${this.currencies}${url}`);

        if( !res.ok ) {
            throw new Error(`Could not fetch ${url}, recieved ${res.status}`)
        }

        return res.json()
    }

    getCurrencies = async (type) => {
        const result = await this.fetchFun(type)
        return result.map(this._transformItems)
    }
    
    _transformItems = (items) => {
        return {
            title: items.name,
            desc: items.code,
            alis: items.buyPrice,
            satis: items.sellPrice

        }

    }
}